@media screen and (max-width: 1266px), screen and (max-device-width: 1266px){
  body{
    .header{
      .container{
        .header__nav-panel{
          &__goods{
            .dropMenu{
              li{
                margin: 20px 0 0 20px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px), screen and (max-device-width: 1024px){
  body{
    .header{
      .container{
        .header__nav-panel{
          &__goods{
            .dropMenu{
              width: 70%;
            }
            .medicalDevices,
            .cosmetics{
              margin-left: -35%;
              left: 42%;
            }
            .kidsGoods{
              margin-left: -35%;
            }
            .medicalEquipment,
            .vitamins{
              margin-right: -35%;
              right: 42%;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .header{
      .container{
        .header__nav-panel{
          &__goods{
            .dropMenu {
              width: 60%;
              top: 0;
              left: 300px;
              li{
                position: relative;
                margin: 5px 0 0 25px;
                width: 100%;
                ul{
                  display: none;
                }
                &:hover{
                  ul{
                    li{
                      display: none;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}